import axios from "axios";
import { createStore } from "vuex";

const store = createStore({
  state() {
    return {
      name: "kim",
      hello: "Hello !",
      like: 30,
      age: 20,
      liked: false,
      more: {},
    };
  },
  mutations: {
    setMore(state, data) {
      state.more = data;
    },
    changeName(state) {
      state.name = "park";
    },
    addAge(state, data) {
      state.age += data;
    },
    clickLike(state) {
      if (state.liked == false) {
        state.like++;
        state.liked = true;
      } else {
        state.liked = false;
        state.like--;
        
      }
    },
  },
  actions: {
    getData(context) {
      axios.get("https://codingapple1.github.io/vue/more0.json")
      .then((a) => {
        console.log(a.data);
        context.commit("setMore", a.data);
      });
    },
  },
});

export default store;
