<template>
  <div>
    <div class="header">
      <ul class="header-button-left">
        <li>Cancel</li>
      </ul>
      <ul class="header-button-right">
        <li v-if="step == 0">MY</li>
        <li v-if="step == 1" @click="step++">Next</li>
        <li v-if="step == 2" @click="publish">Post</li>
      </ul>
      <img @click="step = 0" src="./assets/logo.png" class="logo" />
    </div>
    <h4>
      {{ hello }} {{ 이름 }}
      my age is {{ $store.state.age }}
    </h4>
    <button @click="$store.commit('addAge',10)">나이+1</button>

    <p>{{ $store.state.more }}</p>
    <button @click="$store.dispatch('getData')">더보기</button>

    <Container
      @write="postMsg = $event"
      :imgUrl="imgUrl"
      :instaData="instaData"
      :step="step"
    />
    <button @click="more">더보기</button>
    <div class="footer">
      <ul class="footer-button-plus">
        <input @change="upload" type="file" id="file" class="inputfile" />
        <label for="file" class="input-plus">+</label>
      </ul>
    </div>
  </div>

  <!-- 탭 만들기 기능
    
     <button @click="버튼0 = 0">버튼0</button>
  <button @click="버튼0 = 1">버튼1</button>
  <button @click="버튼0 = 2">버튼2</button>
  <div v-if="버튼0 == 0">내용0</div>
  <div v-if="버튼0 == 1">내용1</div>
  <div v-if="버튼0 == 2">내용2</div> -->
</template>

<script>
import Container from "./components/ContainerView.vue";
import instaData from "./assets/instaData";
import axios from "axios";
import { mapMutations, mapState } from "vuex";

export default {
  name: "App",
  data() {
    return {
      instaData: instaData,
      step: 0,
      countClick: 0,
      버튼0: 0,
      imgUrl: "",
      postMsg: "",
      선택한필터: "",
    };
  },
  mounted() {
    this.emitter.on("clickedFilter", (a) => {
      this.선택한필터 = a;
      console.log(this.선택한필터);
    });
    
  },
  components: {
    Container: Container,
  },
    computed: {
        name() {
          return this.$store.state.name;
        },
        ...mapState(['name', 'hello', 'like','age']),
        ...mapState({이름 : 'name'}),
        ...mapMutations(['setMore','좋아요','addAge'])
      },
  methods: {
    more() {
      axios
        .get(`https://codingapple1.github.io/vue/more${this.countClick}.json`)
        .then((result) => {
          this.instaData.push(result.data);
          this.countClick++;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    tabButton0() {
      this.버튼0 = true;
    },
    upload(e) {
      let 파일 = e.target.files;
      console.log(파일[0]);
      let url = URL.createObjectURL(파일[0]);
      this.imgUrl = url;
      this.step++;
    },
    
    publish() {
      var myInstaData = {
        name: "Kim Hyun",
        userImage: "https://placeimg.com/100/100/arch",
        postImage: this.imgUrl,
        likes: 0,
        date: "May 15",
        liked: false,
        content: this.postMsg,
        filter: this.선택한필터,
      };
      console.log(this.postMsg);
      console.log(this.선택한필터);
      this.instaData.unshift(myInstaData);
      this.step = 0;
    },
  },
};
</script>

