<template>
  <div>
    <!--포스트 -->
    <div v-if="step == 0">
      <PostView
        v-for="(postData, i) in instaData"
        :key="i"
        :instaData="instaData[i]"
      />
    </div>
    <!-- 필터선택페이지 -->
    <div v-else-if="step == 1">
      <div
        :class="선택한필터"
        class="upload-image"
        :style="{ backgroundImage: `url(${imgUrl})` }"
      ></div>
      <div class="filters">
        <FilterBoxView
          :imgUrl="imgUrl"
          :필터="필터"
          v-for="필터 in 필터들"
          :key="필터"
        >
          {{ 필터 }}
        </FilterBoxView>
      </div>
    </div>

    <!-- 글작성페이지 -->
    <div v v-else-if="step == 2">
      <div
        :class="선택한필터"
        class="upload-image"
        :style="{ backgroundImage: `url(${imgUrl})` }"
      ></div>
      <div class="write">
        <textarea
          @input="$emit('write', $event.target.value)"
          class="write-box"
        >
 write!
        </textarea>
      </div>
    </div>
  </div>
</template>
<script>
import PostView from "./PostView.vue";
import FilterBoxView from "./FilterBoxView.vue";
export default {
  components: {
    PostView: PostView,
    FilterBoxView: FilterBoxView,
  },

  data() {
    return {
      필터들: [
        "aden",
        "_1977",
        "brannan",
        "brooklyn",
        "clarendon",
        "earlybird",
        "gingham",
        "hudson",
        "inkwell",
        "kelvin",
        "lark",
        "lofi",
        "maven",
        "mayfair",
        "moon",
        "nashville",
        "perpetua",
        "reyes",
        "rise",
        "slumber",
        "stinson",
        "toaster",
        "valencia",
        "walden",
        "willow",
        "xpro2",
      ],
      선택한필터: "",
    };
  },
  setup() {},
  created() {},
  mounted() {
    this.emitter.on("clickedFilter", (a) => {
      this.선택한필터 = a;
    });
  },
  unmounted() {},
  methods: {},
  props: {
    instaData: Array,
    step: Number,
    imgUrl: String,
  },
};
</script>

<style>
.upload-image {
  width: 100%;
  height: 450px;
  background: cornflowerblue;
  background-size: cover;
}
.filters {
  overflow-x: scroll;
  white-space: nowrap;
}
.filter-1 {
  width: 100px;
  height: 100px;
  background-color: cornflowerblue;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color: white;
  background-size: cover;
}
.filters::-webkit-scrollbar {
  height: 5px;
}
.filters::-webkit-scrollbar-track {
  background: #f1f1f1;
}
.filters::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 5px;
}
.filters::-webkit-scrollbar-thumb:hover {
  background: #555;
}
.write-box {
  border: none;
  width: 90%;
  height: 100px;
  padding: 15px;
  margin: auto;
  display: block;
  outline: none;
}
</style>
