<template>
  <div
    @click="fire"
    :class="필터 + ' filter-item'"
    :style="{ backgroundImage: `url(${imgUrl})` }"
  >
    <slot></slot>
  </div>
</template>
<script>
export default {
  components: {},
  data() {
    return {
      sampleData: "",
    };
  },
  props: {
    imgUrl: String,
    필터: String,
  },
  setup() {},
  created() {},
  mounted() {},
  unmounted() {},
  methods: {
    fire() {
      this.emitter.emit("clickedFilter", this.필터);
    },
  },
};
</script>
<style>
.filter-item {
  width: 100px;
  height: 100px;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color: white;
  background-size: cover;
  background-position: center;
}
</style>
<style scoped>
.filter-item {
  color: black;
  font-weight: bold;
}
</style>
